import { configureStore, createSlice } from "@reduxjs/toolkit";

const initialOptions = { dataDesktop: {}, dataMobile: {}, dataTablet: {}, cssClassName: '', unit: '', section: ''};

const optionsSlice = createSlice({
    name: 'options',
    initialState: initialOptions,
    reducers: {
        updateOptions(state, action) {
            state.dataDesktop = action.payload.input.dataDesktop;
            state.dataMobile = action.payload.input.dataMobile;
            state.dataTablet = action.payload.input.dataTablet;
            state.cssClassName = action.payload.cssClassName;
            state.unit = action.payload.unit;
            state.section = action.payload.section;
        }
    }
});

const store = configureStore({reducer: {options : optionsSlice.reducer}});

export const optionsActions = optionsSlice.actions;

export default store;