import { useEffect, useState, useRef } from 'react';

const Clock = () => {
	const [time, setTime] = useState('');

	useEffect(() => {
		const printTime = setInterval(() => {
			setTime(new Date().toLocaleString());
		}, 1000);

		return () => {
			clearInterval(printTime);
		};
	});

	return (
		<div>
			<p className="subtitle">{time}</p>
		</div>
	);
};

export default Clock;
