import { useState } from 'react';
import { useDispatch } from 'react-redux';
import {optionsActions} from '../store/index';
import { SIZES, SITES } from '../fixtures/properties';
import { buildSiteName } from '../utilities/attributes';
import Preview from './Preview';
import Result from './Result';

const Form = () => {
	const [cssClassName, setCssClassName] = useState('');
	const [unit, setUnit] = useState('');
	const [property, setProperty] = useState('');
	const [section, setSection] = useState('');
	const [pageType, setPageType] = useState('');
	const [adsPreview, setAdsPreview] = useState(false);
	const dispatch = useDispatch();
	const DEVICES = ['desktop', 'mobile', 'tablet'];
	const dataDevices = ['dataDesktop', 'dataMobile', 'dataTablet'];
	const adUnits = ['_dweb', '_mweb', '_dweb'];

	const handleSubmit = e => {
		e.preventDefault();
		const data = {};
		for (let i = 0; i < 3; i++) {
			data[dataDevices[i]] = {
				networkCode: '4654',
				adChannel: 'na',
				adUnit1: buildSiteName(String(property)) + adUnits[i],
				adUnit2: unit,
				adUnit3: section,
				adUnit4: pageType,
				adUnit5: 'na',
				device: DEVICES[i],
				prgads: 'False',
				noad: 'False',
				sizes: SIZES[DEVICES[i]][unit]
			};
		}
		dispatch(optionsActions.updateOptions({ input: data, cssClassName: 'advertisement__container', unit:unit, section: section }));
	};

	const resetForm = () => {
		setCssClassName('');
		setUnit('');
		setProperty('');
		setSection('');
		setPageType('');
	};

	const adPreviewHandler = () => {
		setAdsPreview( prevAdsPreview => !prevAdsPreview );
	}

	/**
	 * Below could be anything
	 */

	const UNITS = ['lb1', 'outstream1', 'imu1', 'outstream2', 'imu2', 'lb2', 'side1'];
	const SECTIONS = ['brand_studio', 'brand_spotlight'];
	const PAGES = ['landingpage', 'articlepage'];

	return (
		<div>
			<div className="columns">
				<div className="column">
					<form onSubmit={handleSubmit}>
						<div className="columns">
							<div className="column is-half">
								<div className="field">
									<label className="label">Classname</label>
									<div className="control">
										<input
											className="input"
											type="text"
											placeholder="advertisement__container"
											value={cssClassName}
											onChange={e => setCssClassName(e.target.value)}
											disabled
										></input>
									</div>
								</div>
							</div>
						</div>
						<div className="columns">
							<div className="column">
								<div className="field">
									<label className="label">Site</label>
									<div className="control">
										<div className="select">
											<select
												id="propertyInput"
												value={property}
												onChange={e => {
													setProperty(e.target.value);
												}}
											>
												<option />
												{SITES.map(property => (
													<option key={property} value={property}>
														{property}
													</option>
												))}
											</select>
										</div>
									</div>
								</div>
							</div>
							<div className="column">
								<div className="field">
									<label className="label">Section</label>
									<div className="control">
										<div className="select">
											<select
												id="sectionInput"
												value={section}
												onChange={e => {
													setSection(e.target.value);
												}}
											>
												<option />
												{SECTIONS.map(section => (
													<option key={section} value={section}>
														{section}
													</option>
												))}
											</select>
										</div>
									</div>
								</div>
							</div>
						</div>
						<div className="columns">
							<div className="column">
								<div className="field">
									<label className="label">Unit</label>
									<div className="control">
										<div className="select">
											<select
												id="unitInput"
												value={unit}
												onChange={e => {
													setUnit(e.target.value);
												}}
											>
												<option />
												{UNITS.map(unit => (
													<option key={unit} value={unit}>
														{unit}
													</option>
												))}
											</select>
										</div>
									</div>
								</div>
							</div>
							<div className="column">
								<div className="field">
									<label className="label">Page Type</label>
									<div className="control">
										<div className="select">
											<select
												id="pageTypeInput"
												value={pageType}
												onChange={e => {
													setPageType(e.target.value);
												}}
											>
												<option />
												{PAGES.map(page => (
													<option key={page} value={page}>
														{page}
													</option>
												))}
											</select>
										</div>
									</div>
								</div>
							</div>
						</div>
						<input className="button is-primary" type="submit" value="Generate" />
						<input
							className="button is-primary mx-1 is-normal"
							value="Reset"
							type="button"
							onClick={resetForm}
						/>
					</form>
				</div>
				<Result showPreview={adPreviewHandler} adsPreview={adsPreview} />
			</div>
			{adsPreview && <Preview />}
		</div>
	);
};

export default Form;
