import React from 'react';
import { render } from 'react-dom';
import { Provider } from 'react-redux';
import { BrowserRouter, Routes, Route, Link } from 'react-router-dom';
import AdMarkUpPage from './layouts/AdMarkUpPage';
import BMSScriptPage from './layouts/BMSScriptPage';
import HomePage from './layouts/HomePage';
import store from './store/index';

const App = () => {
	return (
		<>
			<nav className="navbar" role="navigation" aria-label="main navigation">
				<div className="navbar-brand">
					<Link className="navbar-item" to="/">
						<img
							src="https://www.mediacorp.sg/sites/corporate/themes/mc_corporate_theme/images/logo.svg"
							width="300"
							height="100"
						/>
					</Link>
					{/* To do responsive menu */}
					<a
						role="button"
						className="navbar-burger"
						aria-label="menu"
						aria-expanded="false"
						data-target="navbarBasicExample"
					>
						<span aria-hidden="true"></span>
						<span aria-hidden="true"></span>
						<span aria-hidden="true"></span>
					</a>
				</div>

				<div id="navbarBasicExample" className="navbar-menu">
					<div className="navbar-start">
						<Link to="/ad-markup-generator" className="navbar-item">
							Ad Markup
						</Link>

						<Link to="/bms" className="navbar-item">
							Brand Metrics
						</Link>
					</div>
				</div>
			</nav>
			<Routes>
				<Route path="/" element={<HomePage />} />
				<Route path="/ad-markup-generator" element={<AdMarkUpPage />} />
				<Route path="/bms" element={<BMSScriptPage />} />
			</Routes>
		</>
	);
};

render(
	<React.StrictMode>
		<BrowserRouter>
			<Provider store={store}><App /></Provider>
		</BrowserRouter>
	</React.StrictMode>,
	document.getElementById('root')
);
