import { Fragment, useEffect } from "react";
import { useSelector } from "react-redux";
import '../styles/Preview.css'

const Preview = () => {
    const data = [];
    const DEVICES = ['desktop', 'mobile', 'tablet'];
    const dataDesktop = useSelector((state) => state.options.dataDesktop);
    data.push(
        JSON.stringify(useSelector((state) => state.options.dataDesktop)),
        JSON.stringify(useSelector((state) => state.options.dataMobile)),
        JSON.stringify(useSelector((state) => state.options.dataTablet))
    );
    const section = useSelector((state) => state.options.section);
    const unit = useSelector((state) => state.options.unit);
    const cssClassName = useSelector((state) => state.options.cssClassName);

    const slots = [];

    const createSlot = () => {
        window.googletag = window.googletag || { cmd: [] };
        const adElements = document.getElementsByClassName('advertisement__container');
        for (let i = 0; i < adElements.length; i++) {
            const id = adElements[i].id;
            const jsOptions = JSON.parse(adElements[i].dataset.jsOptions);
            // Parse the attribute jsOptions
            const adUnitPath = `/${jsOptions.networkCode}/${jsOptions.adUnit1}/${jsOptions.adUnit2}/${jsOptions.adUnit3}/${jsOptions.adUnit4}/${jsOptions.adUnit5}`;
            // Define ad slot
            googletag.cmd.push(function () {
                slots.push(googletag
                    .defineSlot(
                        adUnitPath, jsOptions.sizes, id)
                    .addService(googletag.pubads().setTargeting('beta', 'true')));
                googletag.enableServices();
            });
        }
    }

    const display = () => {
        /**
         * Fetch and render the ad
        */
        const adElements = document.getElementsByClassName('advertisement__container');
        for (let i = 0; i < adElements.length; i++) {
            googletag.cmd.push(function () {
                googletag.display(adElements[i].id);
            });
        }
    }

    useEffect(() => {
        createSlot();
        display();
        return () => googletag.destroySlots(slots);
    }, [dataDesktop]);

    return (
        <Fragment>
            <span className="tag is-primary is-medium is-light" >Preview Panel</span>
            <div id="preview-panel">
                {data.map((item, index) => {
                    const idValue = `ad-${DEVICES[index]}-${unit}-1`;
                    return <Fragment key={idValue}>
                        <span className="tag is-primary is-rounded mb-1" >{` ${DEVICES[index]} / ${unit} / ${section} `}</span>
                        <div className={`${cssClassName} mb-4`} id={idValue} data-js-options={item}>
                        </div>
                    </Fragment>
                })}
            </div>
        </Fragment>
    )
}

export default Preview;