import { useSelector } from 'react-redux';

const Result = (props) => {
	const dataDesktop = useSelector((state) => state.options.dataDesktop);
	const dataMobile = useSelector((state) => state.options.dataMobile);
	const dataTablet = useSelector((state) => state.options.dataTablet);
	const section = useSelector((state) => state.options.section);
	const unit = useSelector((state) => state.options.unit);
	const cssClassName = useSelector((state) => state.options.cssClassName);

	const DEVICES = ['desktop', 'mobile', 'tablet'];

	const copyToClipboardHandler = () => {
		if(navigator.clipboard){
			navigator.clipboard.writeText(markup).then(() => {
				console.log("Text copied to clipboard", markup);
			}, (err) => {
				console.log("could not copy text: ", err);
			});
		}else{
			console.error("clipboard API is not available in your browser");
		}
	}

	const escapeDoubleQuoteHandler = dataString => {
		return dataString.replaceAll(/"/gi, '&quot;');
	};

	const createId = ({ device, unit }) => {
		return `ad-${device}-${unit}-1`;
	};

	const createMarkup = () => {
		let markupString = '';

		DEVICES.map(device => {
			let data;

			if (device == 'desktop') {
				data = escapeDoubleQuoteHandler(JSON.stringify(dataDesktop));
			} else if (device == 'tablet') {
				data = escapeDoubleQuoteHandler(JSON.stringify(dataTablet));
			} else {
				data = escapeDoubleQuoteHandler(JSON.stringify(dataMobile));
			}

			markupString += `\n<!-- ${device} / ${unit} / ${section} --> \n <div class="${cssClassName}" id="${createId(
				{
					device,
					unit,
				}
			)}" data-js-options="${data}"></div>\n`;
		});

		return markupString;
	};

	const markup =
		Object.keys(dataDesktop).length > 0
			? createMarkup()
			: 'Not Available';

	const isSuccess = markup != 'Not Available' ? 'textarea is-success' : 'textarea';

	return (
		<div className="column">
			<label className="label">Markup Result</label>
			<div className="field">
				<div className="control">
					<input
						className="button is-primary mx-1 is-normal"
						value={`${props.adsPreview ? "Hide" : "Show"} preview`}
						type="button"
						onClick={props.showPreview}
					/>
					<input
						className="button is-primary mx-1 is-normal"
						value="copy markup"
						type="button"
						disabled={!(Object.keys(dataDesktop).length > 0)}
						onClick={copyToClipboardHandler}
					/>
				</div>
			</div>
			<div className="field">
				<div className="control">
					<textarea className={isSuccess} rows="10" cols="60" value={markup} readOnly></textarea>
				</div>
			</div>
		</div>
	);
};

export default Result;
